@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
.single {
  position: relative;
  z-index: 0;
}
.single .show-before::before {
  top: 50%;
}
.single #banner {
  min-height: unset;
}
.single #banner h2 span {
  color: #cae098;
}
.single #thumbnail img {
  height: auto;
  max-height: 525px;
  object-fit: cover;
}
.single .section-title {
  height: max-content;
}
.single .section-title h1 {
  font-size: 20px;
  line-height: 24px;
}
.single .section-title h1 span {
  color: #fff !important;
}
.single .section-title p {
  font-weight: 300;
  margin-bottom: inherit;
}
.single .section-title p:last-child {
  margin-bottom: 0;
}
.single .section-title:before {
  height: 45%;
}
.single .content-post .section-title {
  padding-left: 18px;
}
.single .content-post .section-title .data-publicacao {
  font-size: 14px;
  line-height: 18px;
}
.single .content-post .section-title::before {
  left: 0;
  height: 100%;
}
.single#single-nossos-servicos .container-slider-imagens-servico {
  background-color: #006635;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev,
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next {
  background-color: transparent;
  border: 1px solid #fff;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev svg,
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next svg {
  width: 16px;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev svg path,
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next svg path {
  transition: all ease 0.3s;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev::before,
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next::before {
  content: "";
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev:hover,
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next:hover {
  background-color: #fff;
  opacity: 1;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev:hover svg path,
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next:hover svg path {
  fill: #006635;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-prev {
  left: -35px;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-next {
  right: -35px;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track {
  display: flex;
  gap: 8px;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item {
  position: relative;
  height: 280px;
  transition: all ease 0.8s;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item img {
  object-fit: cover;
  height: 280px;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item h3 {
  font-size: 14px;
  line-height: 18px;
  color: #fff;
  position: absolute;
  left: 15px;
  bottom: 15px;
  opacity: 0;
  transition: all ease 0.8s;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item.slick-active {
  width: calc(var(--container-width) / 4) !important;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item.slick-active.slick-current {
  width: calc(var(--container-width) / 2) !important;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item.slick-active.slick-current h3 {
  opacity: 1;
}
.single#single-nossos-servicos .container-slider-imagens-servico #slider-imagens-servico .slick-track .item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.48);
}
.single#single-nossos-servicos #icones {
  position: relative;
  background-color: #006635;
  padding: 70px 0 140px;
  z-index: 0;
}
.single#single-nossos-servicos #icones #lista-icones {
  display: grid;
  grid-template-columns: repeat(auto-fit, 220px);
  gap: 10px;
  justify-content: space-evenly;
  background-color: rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  padding: 30px 15px;
  width: 85%;
  margin: 0 auto;
}
.single#single-nossos-servicos #icones #lista-icones .item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  color: #fff;
  text-align: center;
}
.single#single-nossos-servicos #icones #lista-icones .item img {
  max-width: max-content;
}
.single#single-nossos-servicos #icones #lista-icones .item h3 {
  font-weight: 100;
}
.single#single-nossos-servicos #icones #lista-icones .item h3 strong {
  font-weight: bold;
}
.single#single-nossos-servicos #icones #lista-icones .item h3 p {
  margin-bottom: 0;
}
.single#single-nossos-servicos #icones::before {
  height: 100%;
}
.single#single-nossos-servicos #subservicos {
  padding: 60px 0 90px;
}
.single#single-nossos-servicos #subservicos .section-title h2 {
  color: #006635;
}
.single#single-nossos-servicos #subservicos .section-title .options-subservicos {
  display: flex;
  justify-content: flex-start;
  gap: 15px;
}
.single#single-nossos-servicos #subservicos .section-title .options-subservicos button:hover, .single#single-nossos-servicos #subservicos .section-title .options-subservicos button.active {
  background-color: #000;
  border-color: #000;
  color: #fff;
}
.single#single-nossos-servicos #subservicos .section-title::before {
  height: 100%;
}
.single#single-nossos-servicos #subservicos .content-subservico .texto-subservico {
  width: 95%;
  margin: 0 auto;
}
.single#single-nossos-servicos #subservicos .content-subservico .texto-subservico p {
  color: #636363;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo {
  position: relative;
  margin-bottom: 80px;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case {
  position: absolute;
  left: 1%;
  bottom: -15%;
  width: 98%;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case .chapeu-case {
  background-color: #cae098;
  color: #006635;
  font-size: 16px;
  padding: 3px 30px;
  border-radius: 30px;
  margin-left: 2%;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case .texts {
  display: flex;
  gap: 50px;
  background-color: #fff;
  border-radius: 5px;
  padding: 20px 30px 40px;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case .texts h3 {
  font-size: 20px !important;
  font-weight: bold;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case .texts svg {
  width: 20px;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case .texts span {
  font-weight: 500;
  gap: 8px;
}
.single#single-nossos-servicos #subservicos .content-subservico .case-modelo .content-case .texts .conteudo-item {
  font-size: 14px;
  line-height: 18px;
  flex: 1;
}
.single#single-nossos-servicos #cases {
  padding: 0 0 90px;
}
.single#single-nossos-servicos #cases .section-title h2 {
  color: #006635;
}
.single#single-nossos-servicos #cases .section-title::before {
  height: 100%;
}
.single#single-nossos-servicos #nossos-clientes {
  background-color: #fff;
}
.single#single-nossos-servicos #nossos-clientes #slider-nossos-clientes .slick-track {
  display: flex;
  align-items: center;
  gap: 30px;
}
.single#single-nossos-servicos #compromisso {
  position: relative;
  background-color: #006635;
  padding: 100px 0;
  z-index: 0;
}
.single#single-nossos-servicos #compromisso .section-title::before {
  top: -70%;
  height: 240%;
}
.single#single-nossos-servicos #compromisso .compromisso {
  background-color: rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  padding: 45px;
}
.single#single-nossos-servicos #compromisso .compromisso p {
  margin: 0;
}
.single #outras-noticias .section-title {
  padding-left: 25px;
}
.single #outras-noticias .section-title::before {
  left: 0;
  height: 100%;
}